<template>
<div class="box">
    <table class="table table-hover table-bordered">
        <thead>
            <tr>
                <th width="10%">id</th>
                <th width="30%">name</th>
                <th width="40%">content</th>
                <th width="20%">remark</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="data in tableList">
                <td v-text="data.num"></td>
                <td v-text="data.author"></td>
                <td v-text="data.contents"></td>
                <td v-text="data.remark"></td>
            </tr>
        </tbody>
        <tfoot>
            <tr>
                <td colspan="4">
                    <div class="pull-left">
                        <button class="btn btn-default" @click="refresh"></button>
                    </div>
                    <div class="pull-right">
                        <boot-page v-ref:page :async="false" :data="lists" :lens="lenArr" :page-len="pageLen" :param="param"></boot-page>
                    </div>
                </td>
            </tr>
        </tfoot>
    </table>
</div>
</template>

<script>
import bootPage from './components/BootPage.vue'

export default {
    data () {
        return {
            lenArr: [5, 50, 100], // 
            pageLen: 5, // 
            // url: '/bootpage/', // 
            param: {}, // 
            lists: [
                {num: 1, author: 'luozh', contents: 'BootPage', remark: 'dsds'},
                {num: 2, author: 'luozh', contents: 'bootstrap', remark: 'dsds'},
                {num: 3, author: 'luozh', contents: '<boot-page>asyncfalse', remark: 'dsds'},
                {num: 4, author: 'luozh', contents: 'data', remark: 'dsds'},
                {num: 5, author: 'luozh', contents: 'lens', remark: 'dsds'},
                {num: 6, author: 'luozh', contents: 'page-len', remark: 'dsds'},
                {num: 7, author: 'luozh', contents: '{data:[], page_num: 6}, datapage_num', remark: 'dsds'},
                {num: 8, author: 'luozh', contents: 'this.$refs.page.refresh()', remark: 'dsds'}
            ], // 
            tableList: [] // 
        }
    },
    components: {
        bootPage
    },
    methods: {
        refresh () {
            this.$refs.page.refresh()
        }
    },
    events: {

        // 
        'data' (data) {
            this.tableList = data
        },

        // 
        'refresh' () {
            this.refresh()
        }
    }
}
</script>

<style>
@import url('./assets/css/bootstrap.min.css');

.box {
    padding: 100px;
}
</style>


